  const processTextWithHighlight = (text) => {
    // 
    const words = text.split(/([\s.,!?]+)/);
    let result = '';
    let lastColor = null;
    
    words.forEach(word => {
      if (!word || /^\s+$/.test(word)) {
        result += word;
        return;
      }
      
      if (Math.random() < 0.8) {
        let color;
        if (lastColor === 'red') {
          color = 'blue';
        } else if (lastColor === 'blue') {
          color = 'red';
        } else {
          color = Math.random() < 0.5 ? 'red' : 'blue';
        }
        lastColor = color;
        
        result += `<span class="highlight-word ${color}">${word}</span>`;
      } else {
        result += word;
      }
    });
    
    return result;
  };

